<template>
	<div class="card" :style="cardColor">
		<div v-if="this.item && this.item.thumbnail" class="card-thumb">
			<img :src="require(`@/assets/thumb_${this.item.thumbnail}.png`)">
		</div>
		<div class="card-title">
			<template v-if="this.item && this.item.title">
				{{ this.item.title }}
			</template>
		</div>
		<div v-if="this.mode == 'create'">
			<div class="card-title create">
				<input type="text" name="createCardTitle" :class="{'title-error': validateTitleError}" v-model="new_card_title" autofocus="true" ref="createInput" placeholder="New card">
			</div>
		</div>
		<div v-if="this.item && (this.item.comments || this.item.checks || this.item.users || this.item.time)" class="card-footer">
			<div class="left-section">
				<div v-if="this.item.time" class="time">
					<div class="icon">
						<img src="../assets/clock.svg">
					</div>
					<div class="text">
						{{ this.item.time }}
					</div>
				</div>
				<div v-if="this.item.comments" class="comments">
					<img src="../assets/comments.svg">
					<span class="comment-count">{{ this.item.comments.count }}</span>
				</div>
				<div v-if="this.item.checks" class="check">
					<img src="../assets/checkbox.svg">
					<span class="check-count">{{ this.item.checks.count[0] }}/{{ this.item.checks.count[1] }}</span>
				</div>
			</div>			
			<div class="right-section">
				<div v-if="this.item.users" class="users">
					<template v-for="(user, index) in this.item.users">
						<img :class="user_position(index)" :src="require(`@/assets/user_icon_${user}.png`)"/>
					</template>					
				</div>
			</div>
		</div>
		<template v-if="this.mode == 'create'">
			<div class="card-footer">
				<div class="color-palette">
					<div v-for="(color, index) in card_colors" @click="selectCardColor(index)" :class="colorBox(index)" :style="{ backgroundColor : color }"></div>
				</div> 
			</div>
			<div class="save-cancel">
				<div @click="discardNewCard" class="cancel save-card-btn">
					Discard
				</div>
				<div @click="saveNewCard" class="save-card-btn">
					Save
				</div>
			</div>			
		</template>
	</div>
</template>

<script>
	export default {
		props: ["item", "mode", "createparent"],

		data() {
			return {
				card_colors: ["transparent", "#00aaff", "#1bb934", "#ffd400", "#6b6b6b", "#ff4b64"],
				current_color: 0,
				new_card_title: "",
				validateTitleError: false
			}
		},

		computed: {
			cardColor() {
				if (this.item && this.item.card_color && this.item.card_color != "transparent") {
					return "border-left: 3px solid "+this.item.card_color;
				}
				else if (this.mode == 'create' && this.current_color != 0)	{
					return "border-left: 3px solid "+this.card_colors[this.current_color] ;	
				}			
			}
		},

		methods: {
			user_position: function(index) {
				return "user-icon user-" + (index+1);
			},

			colorBox(index) {
				if (this.current_color != index) {
					return "color-box";
				}
				else {
					return "color-box active";
				}
			},

			selectCardColor(index) {
				this.current_color = index;
			},

			discardNewCard() {
				Event.$emit('discard-new-card', this.createparent);
			},

			saveNewCard() {
				console.log(this.new_card_title);
				if (this.new_card_title != "" || this.new_card_title == null) {
					this.validateTitleError = false;
					Event.$emit('save-new-card', [this.createparent, this.new_card_title, this.card_colors[this.current_color]]);
				}
				else {
					this.validateTitleError = true;
				}	
			}
		}
	}	
</script>

<style scoped>
	.card {
		margin-top: 10px;
		padding: 10px;
		border-radius: 3px;
		border: 0.5px solid #e6eaee;
	}

	.card-thumb {
		margin: -12px -10px 0 -10px;
	}

	.card-thumb > img {
	    width: 100%;
	    border-top-left-radius: 5px;
	    border-top-right-radius: 5px;
	}

	.card-title {
		font-size: 12px;
		color: #354052;
		font-weight: 600;
		line-height: 1.36;
	}

	.card-footer {
		display: flex;
		justify-content: space-between;
		margin-top: 10px;
	}

	.left-section {
		display: flex;
		align-items: center;
	}

	.comments > img {
		width: 16px;
		position: relative;
		top: 5px;
	}

	.check {
		margin-left: 15px;
	}

	.check > img {
		width: 12px;
		position: relative;
		top: 2px;
	}

	.comment-count, .check-count {
		color: #7f8fa4;
		font-size: 11px;
		font-weight: 600;
		position: relative;
	    left: 2px;
	}

	.check-count {
		letter-spacing: 1px;
	}

	.users {
		position: relative;
		height: 35px;
		min-width: 35px;
	}

	.user-icon {
		width: 35px;
		height: 35px;
		border-radius: 50%;
		position: absolute;
		border: 0.5px solid #fff;
	}

	.user-1 {
		z-index: 2;
	}

	.user-2 {
		right: 25px;
		z-index: 1;
	}

	.time {
		width: 65px;
		height: 20px;
	    display: flex;
	    justify-content: space-around;
	    align-items: center;
	    background-color: #ff4b64;
	    border-radius: 5px;
	    color: #fff;
	    font-size: 12px;
	    font-weight: 600;
	}

	.time > .icon > img {
		width: 12px;
		height: 12px;
		position: relative;
	    top: 1px;
	    left: 1px;
	}

	.time > .text {
	    position: relative;
	    right: 3px;
	    bottom: 1px;
	}

	.card-title.create > input {
		border: solid 0.5px #dfe3e9;
		border-radius: 3px;
		width: 207px;
		height: 25px;
		font-size: 11px;
		padding: 3px 10px;
	}

	.card-title.create > input.title-error {
		border-color: red;
	}

	.color-palette {
	    width: 100%;
	    display: flex;
	    justify-content: space-around;
	}

	.color-box {
		width: 25px;
		height: 25px;
		cursor: pointer;
		border: 0.5px solid #dfe3e9;
	}

	.color-box.active {
		border-width: 1.5px;
		border-color: #888;
	} 

	.save-cancel {
		display: flex;
		justify-content: flex-end;
		margin-top: 10px;
	}

	.save-card-btn {
	    background-image: linear-gradient(to top, #1991eb, #2da1f8);
    	border: solid 0.5px #1585d8;
	    padding: 3px 5px;
	    width: 45px;
	    font-size: 12px;
	    color: #fff;
	    font-weight: 600;
	    display: flex;
	    justify-content: center;
	    align-items: center;
	    border-radius: 3px;
	    margin-left: 10px;
	    cursor: pointer;
	}

	.save-card-btn.cancel {
		background-image: initial;
		border-color: transparent;
		background-color: #ccd3da;
		position: relative;
		top: 1px;
	}
</style>


